import axios from "axios";
import { useState, useEffect } from "react";
import "./App.css";
import {
  InfoWindow,
  GoogleMap,
  LoadScript,
  Marker,
} from "@react-google-maps/api";

const center = {
  lat: 22.779973198187783,
  lng: 86.16881421555077,
};

function App() {
  const [emp, setEmp] = useState([]);
  const [selectedEmp, setSelectedEmp] = useState("");

  const getEmp = async () => {
    axios.post("/getAllStamp", {}).then((res) => {
      setEmp(res.data?.data);
    });
  };

  console.log(emp);

  useEffect(() => {
    getEmp();
  }, []);

  return (
    <>
      <div className="center">
        <label htmlFor="">Enter Employee Id</label>
        <input
          type="text"
          value={selectedEmp}
          onChange={(e) => {
            setSelectedEmp(e.target.value);
          }}
          style={{ margin: "10px" }}
        />
        <LoadScript googleMapsApiKey="AIzaSyBXu3_xsd-bCytf8HPen5wW8dlw-Mvg-8U">
          <GoogleMap
            // onLoad={handleOnLoad}
            center={center}
            zoom={12}
            mapContainerStyle={{ width: "70%", height: "70vh" }}
          >
            {emp
              ?.filter((e) => {
                if (e?.employee_id === selectedEmp) return e;
              })
              ?.map((item) => (
                <Marker
                  key={item?.id}
                  name={item?.employee_id}
                  position={{
                    lat: parseFloat(item?.latitude),
                    lng: parseFloat(item?.longitude),
                  }}
                  title={item?.timestamp}
                ></Marker>
              ))}
          </GoogleMap>
        </LoadScript>
      </div>
    </>
  );
}

export default App;
